import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { useLocation } from '@gatsbyjs/reach-router';

export const Seo = ({ title, description, children }) => {
  const location = useLocation();
  const isTop = location.pathname === '/';

  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          siteUrl
          title
          description
        }
      }
    }
  `);

  const {
    site: {
      siteMetadata: { siteUrl, title: siteTitle, description: siteDescription }
    }
  } = data;

  title = title ? `${title} | ${siteTitle}` : siteTitle;
  description = description ? description : siteDescription;

  return (
    <>
      <html lang='ja' />
      <title>{title}</title>
      <meta name='description' content={description} />
      <meta property='og:title' content={title} />
      <meta property='og:site_name' content={siteTitle} />
      <meta property='og:type' content={isTop ? 'website' : 'article'} />
      <meta
        property='og:url'
        content={`${siteUrl}${location.pathname}${location.search}`}
      />
      <meta property='og:description' content={description} />
      <meta property='og:image' content={`${siteUrl}/ogp.jpg`} />
      <meta property='og:image:secure_url' content={`${siteUrl}/ogp.jpg`} />
      <meta property='og:image:type' content='image/jpeg' />
      <meta property='og:image:width' content='1200' />
      <meta property='og:image:height' content='630' />
      <meta property='og:locale' content='ja_JP' />
      {children}
    </>
  );
};
